import "react-app-polyfill/ie11";
import "react-app-polyfill/stable";
import React, { useState } from "react";
import { render } from "react-dom";
import styled, { ThemeProvider } from "styled-components";
import { GlobalStyle, theme } from "styles";
import lerp from "lerp";
import { Confetti } from "components/Confetti";
import Icon from "./components/Icon";

const AppStyled = styled.div`
  padding: 2rem;
`;

const App = () => {
  const [update, setUpdate] = useState(0);

  const screenSizeMultiplier = window.innerWidth / 800;

  const burstAmount = Math.floor(
    lerp(100, 150, Math.random() * screenSizeMultiplier)
  );
  const afterBurstAmount = Math.floor(
    lerp(40, 120, Math.random() * screenSizeMultiplier)
  );

  return (
    <AppStyled>
      <Confetti
        key={update}
        burstAmount={burstAmount}
        afterBurstAmount={afterBurstAmount}
        onEnd={() => {
          setUpdate(update + 1);
        }}
      />

      <div
        align="center"
        style={{
          width: "250px",
          margin: "0",
          position: "absolute",
          top: "50%",
          left: "calc(50% - 125px)",
          transform: "translateY(-50%)"
        }}
      >
        <h1>
          {/*Particles: {burstAmount} + {afterBurstAmount}*/}
          <div>
            <Icon
              size={100}
              icon={"clover"}
              style={{ marginBottom: "5px" }}
              color={"#fff"}
              type="color"
            />
            <Icon
              size={100}
              icon={"clover"}
              style={{ marginBottom: "5px" }}
              color={"#fff"}
              type="color"
            />
          </div>
          Buona Fortuna!!! <br />
          Enza <br />
          <div>
            <Icon
              size={100}
              icon={"clover"}
              style={{ marginBottom: "5px" }}
              color={"#fff"}
              type="color"
            />
            <Icon
              size={100}
              icon={"clover"}
              style={{ marginBottom: "5px" }}
              color={"#fff"}
              type="color"
            />
          </div>
        </h1>
      </div>
    </AppStyled>
  );
};

const rootElement = document.getElementById("root");
render(
  <ThemeProvider theme={theme}>
    <GlobalStyle />
    <App />
  </ThemeProvider>,
  rootElement
);
